/*~~~~~~~~~~~~~~
~~~~ LOADER ~~~~
~~~~~~~~~~~~~~~~*/
.loader {
	position: absolute;
	top: calc(50% - 4em);
	left: calc(50% - 4em);
	width: 6em;
	height: 6em;
	border: 1.1em solid rgba(0, 0, 0, 0.2);
	border-left: 1.1em solid #f27127;
	border-radius: 50%;
	animation: load8 1.5s infinite linear;
}

@keyframes load8 {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}

/*~~~~~~~~~~~~~~~~
~~~~ ON-HOVER ~~~~
~~~~~~~~~~~~~~~~~~*/
/* --- Spin --- */
.spin:hover {
	animation: spin ease 1.5s;
	-webkit-animation: spin ease 1.5s;
	-moz-animation: spin ease 1.5s;
	-o-animation: spin ease 1.5s;
	-ms-animation: spin ease 1.5s;
}

@keyframes spin {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}

@-moz-keyframes spin {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}

@-webkit-keyframes spin {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}

@-o-keyframes spin {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}

@-ms-keyframes spin {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}

